<template>
  <div>
    <h1>Let's start your lesson!</h1>
    <!-- <a href="javascript:tagoveApp.max()">Click here to chat with your teacher</a> -->
    <br>
    <h3>Select a language below to use our code editor</h3>

    <!-- <select v-model="lang">
      <option disabled value="">Please select one</option>
      <option>JavaScript</option>
      <option>HTML</option>
      <option>CSS</option>
    </select>
    <span>Selected: {{ lang }}</span>
    <br>
    <br>

    <AceEditor :lang="lang.toLowerCase()"></AceEditor>
      <br> -->

    <codemirror v-model="code" :options="editorOptions"></codemirror>
      <br>

    <textarea class="own-text-editor" rows="40" cols="90"></textarea>
  </div>
</template>

<script>

import AceEditor from '@/components/Editor'
import { codemirror, CodeMirror } from 'vue-codemirror'
import 'codemirror/keymap/sublime';

export default {

  // start of code mirror
  data () {
    return {
      code: 'const a = 10',
      editorOptions: {
        // codemirror options
        tabSize: 4,
        mode: 'text/javascript',
        theme: 'cobalt',
        lineNumbers: true,
        line: true,

        // lang: '',

        // ,codemirror advanced options(You need to manually introduce the corresponding codemirror function script code)
        // sublimeemacsvim
        keyMap: "sublime",
        // Ctrlautocompleteautocompletehint
        extraKeys: { "Ctrl": "autocomplete" },
        // 
        foldGutter: true,
        gutters: ["CodeMirror-linenumbers", "CodeMirror-foldgutter"],
        // 
        styleSelectedText: true,
        highlightSelectionMatches: { showToken: /\w/, annotateScrollbar: true },
        // more codemirror options...
        // hint
      }
    }
  },
  methods: {
    onEditorReady(editor) {
      console.log('the editor is readied!', editor)
    },
    onEditorFocus(editor) {
      console.log('the editor is focus!', editor)
    },
    onEditorCodeChange(newCode) {
      console.log('this is new code', newCode)
      this.code = newCode
    },
  },
  computed: {
    editor() {
      return this.$refs.myEditor.editor
    }
  },
  mounted() {
    console.log('this is current editor object', this.editor)
    // you can use this.editor to do something...
  },
  components: {
    codemirror, CodeMirror,
    // AceEditor,
  },
  // end of code mirror

//Ace Editor
  // components: { AceEditor },
  // data () {
  //   return {
  //     lang: '',
  //   }
  // }
created(){
  tagoveApp.max();
}
}


</script>

<style>
.own-text-editor{
    background: url(http://i.imgur.com/2cOaJ.png);
    background-attachment: local;
    background-repeat: no-repeat;
    padding-left: 35px;
    padding-top: 10px;
    border-color:#ccc;
}
</style>



